*{
    margin: 0;
    padding: 0%;
    
    
  }
  
  div .header{
    background-color: #271f1f;
    border: black;
    box-shadow: black;
    color: aliceblue;
    display: flex;
  }
  div h4{
    padding-top: 10;
    margin-top: 1.5rem;
    font-weight: bold;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
   
  
  .box {
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
  ;
  
    
    
    /* min-height: auto;
    width: 20vw;
    margin: 100px auto;
    background: white;
    border-radius: 5px; */
  }
  
  
  body {
    background-color: #a18cd1;
  }
  
  footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color:#271f1f;
    color: white;
    text-align: center;
    padding: 7px;
  }
  
  .form__group {
    position: absolute;
    padding: 15px 0 0;
    margin-top: 10px;
    width: 55%;
    top: 36%;
    right: 23%;
  }
  .text-area{
  
    border: none;
    height: 135px;
    /* margin: 10px; */
    width: 100%;
    font-size: 1.5rem;
}
  
  .form__field {
    font-family: inherit;
    width: 100%;
    border: 0;
    border-bottom: 2px solid #9b9b9b;
    outline: 0;
    font-size: 1.3rem;
    color: black;
    padding: 7px 0;
    /* background: transparent; */
    transition: border-color 0.2s;
  }
  .form__field::placeholder {
    color: transparent;
  }
  .form__field:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
  
  .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #9b9b9b;
  }
  
  .form__field:focus {
    padding-bottom: 6px;
    font-weight: 700;
    border-width: 3px;
    border-image: linear-gradient(to right, rgb(100, 100, 1), #38ef7d);
    border-image-slice: 1;
  }
  .form__field:focus ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;  
    color: #d19f15;
    font-weight: 700;
  }
  
  /* reset input */
  .form__field:required, .form__field:invalid {
    box-shadow: none;
  }
  
  .btn {
    color: rgb(156, 38, 38);
    background-color: #d19f15;
    border-radius: 50%;
     
  } 
  
  .DeleteOutlineIcon{
    color: rgba(255, 0, 0, 0.417);
    
    border-radius: 50%;
    border: none;
     
  }
  
  
  /* Float four columns side by side */
  .column {
    float: left;
    width: 25%;
    padding: 0 10px;
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
    
  
  
  }
  
  /* Remove extra left and right margins, due to padding */
  .row {margin: 0 -5px 5px 5px;
    box-sizing: border-box;
  }
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Responsive columns */
  @media screen and (max-width: 1000px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }
  
  /* Style the counter cards */
  .complete {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 16px;
    text-align: center;
    /* background-color: #f1f1f1; */
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
    display: flex;
    flex-direction: column;
  
  }

  .active {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 16px;
    text-align: center;
    /* background-color: #f1f1f1; */
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
    display: flex;
    flex-direction: column;
    /* position: absolute;
    bottom: 0%;
    left: 2%; */
  }

  .DeleteOutlineIcon{
    align-self: flex-end;
    border: none;
    
  
  }
  .DeleteOutlineIcon:hover{
    motion: 2%;
    cursor: pointer;
    background-color: rgba(255, 0, 0, 0.253);

  }



  .title{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2%;
    font-size: 1.5rem;
  }


  .search{
    position: absolute;
    top: 3%;
    left: 65%;
    height: 5%;
    width: 23%;
    font-size: 1.4rem;
  }

  .submit{
    font-size: 1.3rem;
    color: #38ef7d;
  }

